;(function() {


    var SectionSlider = {
            mySwiper: null,
            sliderID: null,

            _init : function(){
                    var self = SectionSlider;

                    self.sliderID = $("#mobile-slider");

                   $(window).resize(self._onResize);

                    if(self._sizeWindow()){
                        self._addSlider();
                    }

                  },
            _addSlider : function() {

                        var self = SectionSlider;

                            self.sliderID.addClass('swiper-container');
                            self.mySwiper  = new Swiper('.swiper-container', {
                                pagination: '.swiper-pagination',
                                slidesPerView: 'auto',
                                centeredSlides: true,
                                spaceBetween: 0
                            });

                        
                      },
            _sizeWindow : function () {
                return ($(document).width() <= 690);
            },
            _onResize :function(){
                var self = SectionSlider,
                    isSizeWindow = self._sizeWindow();

                    if(isSizeWindow && (self.mySwiper)!=null){
                        
                        self.mySwiper.onResize();
                    } else if(isSizeWindow && (self.mySwiper)==null){
                        self.sliderID.addClass('swiper-container');
                        self._addSlider();
                        
                    } else if(!isSizeWindow && (self.mySwiper)!=null){
                        self.mySwiper.destroy();
                        self.mySwiper = null;
                        self.sliderID.removeClass('swiper-container');
                    }
            }
    };

      $.fn.list_ticker = function(options){
        
        var defaults = {
          speed:4000,
          effect:'slide'
        };
        
        var options = $.extend(defaults, options);
        
        return this.each(function(){
          
          var obj = $(this);
          var list = obj.children();
          list.not(':first').hide();
          
          setInterval(function(){
            
            list = obj.children();
            list.not(':first').hide();
            
            var first_li = list.eq(0);
            var second_li = list.eq(1);
            
            if(options.effect == 'slide'){
                first_li.slideUp();
                second_li.slideDown(function(){
                    first_li.remove().appendTo(obj);
                });
            } else if(options.effect == 'fade'){
                first_li.fadeOut(function(){
                    second_li.fadeIn();
                    first_li.remove().appendTo(obj);
                });
            }
          }, options.speed);
        });
      };

    $(document).ready(SectionSlider._init);

    $(document).ready(function(){
        $('#slideshow').list_ticker({
            speed:5000,
            effect:'fade'
        });
    });

})();